.App {
  display: grid;
  justify-self: center;
  grid-template-columns: max-content auto;
  grid-template-rows: max-content auto;
  height: 100vh;
  grid-template-areas:
    "header header"
    "nav main";
}

.App.hideNav {
  grid-template-areas:
    "header header"
    "main main";
}

.header {
  grid-area: header;
  background-color: #e1e4e5;
}

.nav {
  grid-area: nav;
  width: max-content;
}

.main {
  background-color: white;
  grid-area: main;
  overflow: auto;
  padding: 20px;
}

.navBarMenuCollapsed {
  width: 50px;
  justify-content: center;
}
