.loadingBackground {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  position: absolute;
  top: 0;
  z-index: 999;
}

.loadingContainer {
  display: flex;
  align-items: center;
  flex-flow: column;
  color: rgb(78, 90, 96);
  font-family: Arial, Helvetica, sans-serif;
}

.loadingText {
  font-size: 1rem;
  font-weight: bold;
  cursor: default;
}
