.headerContainer {
  display: flex;
  flex-flow: column;
}

.mainHeader {
  display: flex;
  justify-content: space-between;
  background-color: #100e5a;
  color: white;
}

.mainHeaderLeft {
  display: flex;
  align-items: center;
  width: 100px;
}

.headerLogoImage {
  height: 2rem;
  margin: 1rem;
  margin-right: 0;
}

.headerText {
  font-family: Arial, Helvetica, sans-serif;
}

.mainHeaderRight {
  display: flex;
  align-items: center;
}

.headerUserImage {
  margin: 1rem;
  cursor: pointer;
}

.subHeader {
  display: flex;
  background-color: #e1e4e5;
  align-items: center;
}

.subHeaderLeft {
  display: flex;
  align-items: center;
  width: 50%;
}

.subHeaderRight {
  display: flex;
  align-self: flex-end;
  width: 50%;
}

.subHeaderHome {
  font-size: 1.5rem;
  margin: 1rem;
  margin-right: 0;
  cursor: pointer;
  color: #100e5a;
}

.tenantName {
  color: #384750;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  width: 50rem;
}

.mainSeparator {
  border: 1px solid rgba(255, 255, 255, 0.5);
  height: 2rem;
  margin: 0 1rem;
}

.subHeaderSeparator {
  border: 1px solid rgba(0, 0, 0, 0.5);
  height: 2rem;
  margin: 0 1rem;
}

.testTenant {
  border-bottom: 5px solid #ffc107;
}
