.navButtonMainContainer {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 1rem 0;
  color: black;
  text-decoration: none;
}

.navButtonMainContainer:hover {
  background-color: rgba(228, 247, 255, 1);
  color: black;
  text-decoration: none;
}

.navButtonIcon {
  font-size: 1rem;
  margin: 0 1rem;
}

.navTitle {
  display: flex;
  flex-wrap: nowrap;
  margin: 0 1.5rem 0 0;
  font-size: 1.2rem;
}

.subButtonsArrow {
  margin-right: 15px;
  font-size: 1.5rem;
}

.collapsed .sunButtons {
  display: none;
}

.collapsed .navButtonIcon {
  margin-left: 0;
}

.collapsed .navTitle {
  display: none;
}

.collapsed .subButtonsArrow {
  display: none;
}

.hideSubButtons .navButtonMainContainer {
  display: none;
}
