.organizationProfileContainer {
  display: flex;
  flex-flow: column;
  max-width: 800px;
}

.orgProfileTitle {
  margin: 0 1rem;
}

.section {
  display: flex;
  margin-bottom: 30px;
}

.leftSection {
  display: flex;
  flex-flow: column;
  flex-basis: 50%;
}

.rightSection {
  display: flex;
  flex-flow: column;
  flex-basis: 50%;
}

.primaryContact {
  display: flex;
}

.editButton {
  margin: 20px;
  min-width: 100px;
}

.inputField {
  margin: 1rem;
}

.toggleField {
  margin: 1rem;
}

.orgProfileSaveAndCancelButtons {
  display: flex;
  margin: 0 1rem;
}

.orgProfileCancelButton {
  margin: 0 1rem;
}
