.popupBackground {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
}

.popupContainer {
  display: flex;
  flex-flow: column;
  background-color: white;
  border-radius: 5px;
  box-shadow: 10px 10px 10px rgb(49, 49, 49);
  margin: 5%;
}

.popupHeader {
  display: flex;
  justify-content: space-between;
  cursor: default;
}

.closeButton {
  box-sizing: content-box;
  cursor: pointer;
  font-size: 1.5rem;
  padding: 10px;
  margin: 5px;
}

.popupTitle {
  margin: 20px;
  font-size: 2rem;
  font-weight: bold;
}

.popupContent {
  margin: 20px;
}
